import React from "react";
import styled, { css } from "styled-components";
import tw from "twin.macro";
import { slide as Menu } from "react-burger-menu";
import { useMediaQuery } from "react-responsive";
import { SCREENS } from "../Responsive";
import MenuStyles from "./MenuStyles";

const ListContainer = styled.ul`
  ${tw`
    flex
    list-none
    `};
`;
const NavItem = styled.li`
  //<{ menu?: any }>
  ${tw`
    text-sm
    md:text-base
    text-black
    font-medium
    mr-1
    md:mr-5
    cursor-pointer
    transition
    duration-300
    ease-in-out
    hover:text-gray-700
    `}
`;

const NavItems = () => {
  const isMobile = useMediaQuery({ maxWidth: SCREENS.sm });

  if (isMobile) {
    //TODO: :Style the mobile menu item text (sidebar)
    return (
      <Menu right styles={MenuStyles}>
        <ListContainer>
          <NavItem>
            <a href="/">Home</a>
          </NavItem>
          <NavItem>
            <a href="/">Cars</a>
          </NavItem>
          <NavItem>
            <a href="/">Services</a>
          </NavItem>
          <NavItem>
            <a href="/">Contact Us</a>
          </NavItem>
        </ListContainer>
      </Menu>
    );
  }
  return (
    <ListContainer>
      <NavItem>
        <a href="/">Home</a>
      </NavItem>
      <NavItem>
        <a href="/">Cars</a>
      </NavItem>
      <NavItem>
        <a href="/">Services</a>
      </NavItem>
      <NavItem>
        <a href="/">Contact Us</a>
      </NavItem>
    </ListContainer>
  );
};

export default NavItems;
